<template>
  <div class='wrapper-bg'>
    <div class="wrapper">
      <div class="content">
        <div class="w-left">
          <ctitle :title="'区域年度信访量排行'" class="w-l-title"></ctitle>
          <div class="echarts">
            <horizontal-bar :params="xfbmBarParams"></horizontal-bar>
          </div>
        </div>
        <div class="w-center">
          <div class="title-outer">
            <span>全省信访量</span>
          </div>
          <div class="c-bg"></div>
          <div class="detail">
            <p class="allNumber">
              <span class="counts">
                <incNumber class="number" :incNumber="jctotal"></incNumber>
              </span>件次
            </p>
          </div>
          <Map :params="mapParams" class="map"></Map>
        </div>
        <div class="w-right">
          <div class="r-top">
            <ctitle :title="'信访量月度走势'"></ctitle>
            <div class="echarts">
              <LinesR :params="ydLinesParams"></LinesR>
            </div>
          </div>
          <div class="r-bottom">
            <ctitle :title="'年度三率指数'"></ctitle>
            <div class="echarts">
              <radar class="chart" :params="zyzbRadarParams"></radar>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {get_http} from "@/api/index";
import horizontalBar from "@/components/bar/HorizontalBar1";
import ctitle from "@/components/title/index";
import Map from "@/components/map/Mapyn";
import incNumber from "@/components/incNumber/index";
import LinesR from "@/components/line/LinesR1";
import radar from "@/components/radar/radar";
	export default {
		name: 'zlfx',
		components: {
			horizontalBar,
			ctitle,
			Map,
			incNumber,
			LinesR,
			radar
		},
		data() {
			return {
				// 1.区域年度信访量排行
				xfbmBarParams: {
					data: [],
					canvasStyle: {
						width: "100%",
						height: "100%"
					}
				},
				//2.地图
				mapFlag: false,
				jctotal: 0,
        mapParams: {
          canvasStyle: {
            width: "100%",
            height: "98%",
          },
          data: [],
          options: {
            scatterColor: '#3cfff8',
            emphasisColor: '#ffd200',
            isHome: true,
            JSON: "530000",
            xName: "name",
						yName: "value_g0_1",
						top: 0,
            map: {
              "value_g0_1": "总数",
              "value_g1_1": "来信",
              "value_g1_2": "来访",
              "value_g1_3": "网上投诉"
            },
            carousel: false
          }
				},
				//3.信访量月度走势
        ydLinesParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          colors: [
            {
              line: "#1a82f7",
            },
            {
              line: "#e11770",
            },
            {
              line: "#1a82f7",
            },
            {
              line: "#a1d871",
            },
            {
              line: "#ff825b",
            }
          ],
          option: {
            showSymbol: true,
            splitNumber:4,
            axisLabelColor: "#fff",
            yaxisLabelColor: "#82d8ff",
						legendfontSize: 18,
						splitXLineShow: true,
            splitLineShow: false,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisCategoryName: "月",
            axisYName:"",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: true,
            areaShow: 0,
            smoothMonotone: "none",
            dataZoomshow: false,
            left: "10%",
            right: "10%",
            top: "15%",
            bottom: "10%",
            legendTop: "5%",
            legendRight: "10%",
          },
          map: {
            value_g0_1: "去年",
            value_g1_1: "今年"
          },
          xName: "name"
				},
				//5.年度三率指数
        zyzbRadarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            center: ['50%', '60%'],
            radius: '80%'
          }
        },
			}
		},
		mounted() {
			this.getMapAPI()
			// this.getAPI(this.year)
		},
		methods: {
			// 1.全省信访量-地图
      getMapAPI() {
        // 处理接口参数
        // 省级
        // if (this.curCity === '广东省') {
        //   this.shej_02 = this.curCity;
        //   this.shij_02 = 'invalid';
        //   this.xj_02 = 'invalid';
        //   this.nameP = 'shij_02';
        // } else {
        //   // 市级
        //   if (this.shijCity) {
        //     this.shej_02 = '广东省';
        //     this.shij_02 = this.shijCity;
        //     this.xj_02 = 'invalid';
        //     this.nameP = 'xj_02';
        //   } else {
        //     // 县区级
        //     this.shej_02 = '广东省';
        //     this.shij_02 = this.shijCity;
        //     this.xj_02 = this.curCity;
        //     this.nameP = 'invalid';
        //   }
        // }

        get_http("xftstjfxZlfxQsxflMap", {
          // shej_02: this.shej_02,
          // shij_02: this.shij_02,
          // xj_02: this.xj_02,
          // name: this.nameP,
        })
          .then(res => {
            this.mapParams.data = res.data.data;
            this.mapParams.options.map = res.data.map;
            this.mapFlag = true;
            this.getAPI(this.year);
          })
          .catch(err => {
            console.log(err);
					});
					

      },
			getAPI(year) {
				// 1.区域年度信访量排行（左侧柱图）
        get_http("xftstjfxZlfxNdxflph", {year: year})
				.then(res => {
					this.xfbmBarParams.data = res.data.data.sort((a, b) => {return a.value - b.value;});
				})
				.catch(err => {
					console.log(err);
				});
				// 3.全省信访量（总数）
				get_http("xftstjfxZlfxQsxflTotal", {year: year})
				.then(res => {
					this.jctotal = res.data.data[0].value
				})
				.catch(err => {
					console.log(err);
				});
				// 4.信访量月度走势
        get_http("xftstjfxZlfxXflydzs", {year: year})
          .then(res => {
            this.ydLinesParams.map = res.data.map;
            this.ydLinesParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
					});
				// 5.年度三率指数
        get_http("xftstjfxZlfxNdslzs", {year: year})
          .then(res => {
            this.zyzbRadarParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
			}
    },
    computed: {
      year() {
        return this.$store.state.year;
      }
    },
    watch: {
      year(val) {
        this.getAPI(val);
      }
    }
	}
</script>

<style lang="stylus" scoped>
.wrapper-bg
  background url("../../../assets/wrapper-bg2.png") no-repeat center
  background-size 100% 100%
  width 100%
  height 100%
.wrapper
	position relative
	top 0.45rem
	height calc(100% - 45px)
	.content
		width 100%
		height 100%
		display flex
		padding .2rem .3rem
		box-sizing border-box
		display flex
		.w-left
			width 4.8rem
			height 100%
			position relative
			.echarts
				height calc(100% - 0.28rem)
				box-sizing border-box
		.w-right
			flex 1
			display flex
			flex-direction column
			.r-top
				flex 1
				display flex
				flex-direction column
				.echarts
					flex 1
			.r-bottom
				flex 1
				display flex
				flex-direction column
				.echarts
					flex 1
		.w-center
			flex 1
			position relative
			.map-chart
				background none
				position absolute
				top -30px
			.title-outer
				display flex
				span
					color #fff
					font-size 22px
					font-weight 700
					line-height 28px
					text-shadow 0 0 10px #276bf4
					margin 0 auto
			.detail
				.allNumber
					font-size 0.2rem
					color #5aceff
					display flex
					align-items flex-end
					justify-content center
					margin-left 0.55rem
					margin-top 0.1rem
					.bgNumber
						display inline-block
					.number
						font-size 0.5rem
						color #fff
						text-shadow 0 0 6px #276bf4, 0 0 10px #276bf4, 0 0 20px #276bf4, 0 0 30px #276bf4, 0 0 40px #276bf4
						margin-right 0.15rem
			.c-bg
				position absolute
				width 100%
				height 100%
				z-index 0
				top 0
				left 0
				background url("../../../assets/map-bg.png") no-repeat center bottom
</style>
